
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

#tilefish_commentary {
  display: block;
  margin-left: -45px;
}
#tilefish_commentary ul {
  list-style-type: none;
}
#tilefish_commentary li {
  display: block;
}
a {
  color: #42b983;
}
#tilefish_funfacts {
  max-width: 600px;
  display: block;
  font-size: 150%;
  text-align: center;
  margin: auto;
  margin-top: 25px;
}
#tilefish_funfacts ol {
  max-width: 85%;
  text-align: left;
  margin: auto;
}
#tilefish_funfacts li {
  font-size: 70%;
}
#tilefish_sources {
  display: block;
  margin-left: -45px;
  font-size: 35%;
}
#tilefish_sources ul {
  list-style-type: none;
}
#tilefish_sources li {
  display: block;
}
#header #seaweed_image{
  width: 10vw;
}
#header #tilefish_image{
  width: 50vw;
}
h1 {
  font-size: 300%;
}
